@import url(https://fonts.googleapis.com/css2?family=Rowdies:wght@300;400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*App js*/
.app{
  
  font-family: 'Rowdies', cursive;
}


/*Header js*/
.header{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 6rem;
  margin-left: 2rem;
  margin-right: 2rem;
}

.header-button{
  color: #203147;
  padding: 10px;
  border: #203147 3px solid;
  border-radius: 10px;
  transition: .4s;
}

.header-button:hover{
  color: white;
  background: #203147;
  cursor: pointer;
  transition: .4s;
}

/*Showcase js*/
.showcase-container{
  height: 90vh;
  padding-bottom: 3rem;
}

.showcase{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  height: 100%;
  width: 100%;
}

.showcase-head-text{
  font-size: 36px;
  font-weight: 500;
}

.showcase-sub-text{
  font-size: 18px;
  font-weight: 300;
}

.showcase-img-container{
  display: flex;
  justify-content: center;
  padding-top: 1rem;
}
.showcase-img{
  height: 50%;
  width: auto;
}

/*About Page*/
.about-background{
  background: rgb(32,49,71);
background: linear-gradient(180deg, rgba(32,49,71,1) 75%, rgba(255,255,255,1) 75%);
}

.about-header{
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 700;
  color: #203147;
  font-size: 50px;
  display: flex;
  justify-content: right;
  margin-bottom: -6rem;
}

.about-text{
  color: white;
 
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column ;
  text-align: center;
  padding: 5rem 25% 3rem  25%;
  font-weight: 300;
}

.about-text p{
  font-size: 20px;
}

.about-container{
  display: flex;
  margin: 1rem 3rem 1rem 3rem;
  background-color: white;
  border-radius: 16px;
} 

.about-column{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 2rem 1rem 1rem 1rem;
  font-weight: 300;
}

.about-column div{
  padding-bottom: 1rem;
}

.about-icon{
  color: #203147;
}

.about-blue-text{
  font-size: 1.2rem;
  color: #203147;
}

/*Recent Work*/
.recent-work{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 2rem;
}

.recent-work-title{
  text-align: center;
  font-weight: 300;
  margin-bottom: 1rem;
}

.recent-work-title a{
  text-decoration: none;
  color: #203147;
}

.recent-work-project-container{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  grid-gap: 1rem;
  gap: 1rem;
}

.recent-work-project{
  position: relative;
}

.recent-work-project img{
  height: 250px;
  width: 250px;
  border-radius: 20px;
}
.recent-work-project-info{
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  background: #203147;
  color: white;
  text-align: center;
  height: 100%;
  position: absolute;
  top: 0;
  border-radius: 20px;
  opacity: 0;
  transition: .3s;
  padding-left: 7px;
  padding-right: 7px;
}

.recent-work-project-info:hover{
  opacity: 1;
  transition: .3s;
}

.recent-work-project-info a{
  text-decoration: none;
  color: white;
  border: #58c758 2px solid;
  padding: 7px;
  border-radius: 10px;
  transition: .3s;
}

.recent-work-project-info a:hover{
  background: #58c758;
  transition: .3s;
}

/* Footer */
.footer{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  background: #203147;
  color: white;
  padding-bottom: 3rem;
}

.footer img{
  padding: 2rem;
}

.footer p{
  margin-bottom: 3rem;
}

.footer-links-container{
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 30vw;
}

.footer-links svg{
  color: white;
  border: grey 2px solid;
  padding: 10px;
  border-radius: 50%;
}

.footer-links svg:hover{
  color: #203147;
  background: white;
  border-color: white;
}


/*Media Queries */
@media screen and (max-width: 700px){
  .about-container{
    flex-direction: column;
  }

  .recent-work-project-container{
    grid-template-rows: 1fr 1fr 1fr;
    grid-template-columns: 1fr 1fr;
  }

  .footer-links-container{
    width: 50vw;
  }

}

@media screen and (max-width: 535px){
  .recent-work-project-container{
    grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr;
    grid-template-columns: 1fr;
  }

  .footer-links-container{
    width: 70vw;
  }
}

